import React from "react";
import Card from "./Card";
import emojipedia from "../emojipedia";

function createCard(pedia) {
  return (
    <Card
      key={pedia.id}
      emoji={pedia.emoji}
      name={pedia.name}
      descr={pedia.meaning}
    />
  );
}
function App() {
  return (
    <div>
      <h1>
        <span>emojipedia</span>
      </h1>
      <dl className="dictionary">{emojipedia.map(createCard)}</dl>
    </div>
  );
}

export default App;
